import Vue from 'vue'
import VueRouter from 'vue-router'


import Inicio from '../views/Inicio.vue'
import Creadores_estrella from '../views/Creadoresestrella.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'inicio',
    component: Inicio
  },
  {
    path: '/creadoresestrella',
    name: 'creadoresestrella',
    component: Creadores_estrella
  }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: "active",
  linkExactActiveClass: "active"
})

export default router
